@import url(https://fonts.googleapis.com/css?family=Noto+Serif+TC&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Serif+TC&display=swap);
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "sans-serif";
    font-family: 'Noto Sans TC',"Microsoft YaHei","黑體","宋體", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #001935; */

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


html,
body{
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    /* background-color: #DFDFDF; */
    background-color: #13203D;
}

input {
    background-color: transparent;
}

input:focus {
    background-color: transparent;
    outline: none;
}

button:active{
    outline: none;
}

.container{
    margin: 50px auto 0 auto;
    max-width: 1200px;
}

.nav-container {
    margin: auto;
}

.nav-container svg {
    color: #fff;
}
.user-pic {
    height: 64px;
    width: 64px;
    background-color: #001935;
    display: block;
    border-radius: 3px;
    background-size: cover;
    overflow: visible;
    margin-right: 20px;
    object-fit: cover;
}

.post-body {
    width: 100%;
    display: flex;
}

.post-card {
    width: 540px;
    font-size: 14px;
    background: #fff;
    padding: 15px 0 0;
    word-wrap: break-word;
    border-radius: 3px;
    margin-bottom: 20px;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, .1);
    border: 1px solid #D6DBD2;
    position: relative;
}

.mainImage {
    width: 100%;
    margin-bottom: 20px;
    margin-top: 20px;
}

.user-id {
    padding-left: 20px;
    font-size: 16px;
    margin-bottom: 10px;
    text-decoration: none;
    color:#000000;
}

.card-title {
    font-weight: 400;
    font-size: 36px;
    line-height: 47px;
    padding-left: 20px;
    margin-top: 15px;
    /* margin-bottom: 10px; */
}

.card-content {
    margin-top: 20px;
    padding-right: 20px;
    margin-bottom: 30px;
    padding-left: 20px;
}


.card-hashtag {
    color: #8f8f8f;
    display: inline;
    font-size: 13px;
    padding-left: 20px;
}

.post-details {
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
}

.interact-icons {
    display: flex;
}

.interact-icon {
    width: 20px;
    margin-left: 15px;
    cursor: pointer;
    z-index: 999;
}

.post-time {
    padding-left: 20px;
    font-size: 10px;
    color: #8f8f8f;
    margin-top: 5px;
}

.loading {
    color: #ffffff;
}

.reply-box {
    position: relative;
    display: inline-block;
    z-index: 999;
}

.mask {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
}

.quoteHere{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
    line-height: 2;
    margin-top: 20px;
    font-family: 'Noto Serif TC',serif;
}

.quote-body {
    font-size: 34px;
    line-height: 42px;
    margin-left: 15px;
    margin-right: 15px;
    font-family: 'Noto Serif TC',serif;
    width: 100%;
}

.quote-from{
    padding-left: 20px;
    font-size: 14px;
}

.body-content{
    display: grid;
}
.main {
    width: 100%;
}

.main-content{
    display: flex;
    justify-content: center;
}

.left{
    margin-right: 30px;
    margin-top: 30px;
}

.right{
    margin-top: 30px;
}
.post-bar {
    margin-bottom: 20px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: 500;
}

.post-section {
    background-color: #ffffff;
    height: 100px;
    width: 540px;
    border-radius: 5px;
    display: flex;
    justify-content: space-evenly;
    list-style-type: none;
    padding: 10px 0 20px 0;
    box-sizing: border-box;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, .1);
    padding-bottom: 15px;
    border: 1px solid #D6DBD2;
}

.post-section img {
    position: relative;
    width: 40px;
    cursor: pointer;
    -webkit-transition: top 0.5s;
    transition: top 0.5s;
}

.avatar-Pic {
    z-index: 999;
    height: 64px;
    width: 64px;
    background-color: #001935;
    display: block;
    border-radius: 3px;
    background-size: cover;
    overflow: visible;
    margin-right: 20px;
    object-fit: cover;
}

.post-section img:hover {
    top: -3px
}

.icon-text {
    margin-top: 5px;
    color: rgba(0, 0, 0, 0.65);
    font-size: 15px;
}

.post-text,
.post-pic,
.post-quote,
.post-video {
    text-align: center;
    margin-top: 10px;
    line-height: 1;
}

.post-text,
.post-pic,
.post-quote {
    border-right: rgba(0, 0, 0, 0.3) 0.5px solid;
    padding-right: 50px;
    /* height: 60px; */
    /* border: 0;
    height: 1px; */
    /* background-image: -webkit-linear-gradient(top, #f0f0f0, #8c8b8b, #f0f0f0);
    background-image: -moz-linear-gradient(top, #f0f0f0, #8c8b8b, #f0f0f0);
    background-image: -ms-linear-gradient(top, #f0f0f0, #8c8b8b, #f0f0f0);
