}

.post-title {
    border: none;
    font-size: 28px;
    display: block;
    margin-bottom: 10px;
    padding-top: 10px;
}

.post-title:focus {
    outline: none;
}

.upload img {
    width: 20px;
    cursor: pointer;
}

.upload img:hover {
    bottom: -3px;
}

.post-content {
    border: none;
    line-height: 21px;
    font-size: 14px;
    width: 95%;
    height: auto;
    resize: none;
}

.post-content:focus {
    outline: none;
}

.upload {
    display: flex;
    justify-content: flex-end;
}

.close-btn {
    display: inline-block;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    padding: 5px 7px;
    border-radius: 2px;
    line-height: 1.4;
    text-decoration: none;
    border: 1px solid #9da6af;
    background-color: #9da6af;
    color: #fff;
    font-size: 12px;
}

.close-btn:focus {
    outline: none;
}

.close-btn:active {
    background-color: #B0B5B3;
}

.post-btns {
    display: flex;
    justify-content: space-between;

}

.send-btn {
    border-color: #00b8ff;
    background-color: #00b8ff;
    color: hsla(0, 0%, 100%, .5);
    color: #001935;
    border-radius: 2px;
    cursor: pointer;
    padding: 5px 7px;
    border-radius: 2px;
